html {
  padding: 0;
  margin: 0; }
body {
  font-family: 'Montserrat', sans-serif;
  padding: 0;
  margin: 0;
  font-size: 14px; }

*,*:after,*:before {
  box-sizing: border-box; }
*:focus {
  border: none;
  outline: none; }
a {
  cursor: pointer;
  color: black;
  text-decoration: none; }



// LOGO
.logo img {
  width: 100%;
  height: auto; }
//404
.page-404 {
  max-width: 100%;
  font-family: Quicksand;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center; }
.page-404__title {
  font-size: 1.4rem;
  color: red;
  margin-bottom: 40px; }
.page-404__title--long {
  font-size: 0.7rem; }
.page-404__subtitle {
  font-size: 1rem;
  color: #BC9CFF; }
//banner
.banner {
  font-family: Quicksand;
  font-weight: bold;
  text-align: center; }
.banner__title {
  text-transform: uppercase; }
.banner__subtitle {
  color: #BC9CFF; }



